@import url('https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap');

:root {
  --sidebar-flex: 1.5;
  --condiv-flex: 6;
  --fontsize-p: 1.2rem;
  --fontsize-nav: 1.1rem;
  --typing-font: 1.8rem;
}

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Raleway';
  background: #dde1e7;
}

.App {
  display: flex;
  width: 100vw;
  min-height: 100vh;
}

nav {
  width: 98%;
  position: absolute;
  top: 3%;
  height: 7.5%;
  left: 1%;
  background: #dde1e7;
  border-radius: 30px;
  padding: 0;
  text-align: center;
  box-shadow: -3px -3px 7px #ffffff73, 3px 3px 5px rgba(18, 18, 19, 0.288);
}

.condiv {
  width: 90%;
  height: auto;
  position: absolute;
  top: 15%;
  left: 4%;
  border-radius: 30px;
  line-height: 1;
  padding-left: 10px;
  padding-right: 10px;
  background: #dde1e7;
  box-shadow: -3px -3px 7px #ffffff73, 3px 3px 5px rgba(18, 18, 19, 0.288);
}

.home {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;

}

.profilepic {
  border-radius: 50%;
  width: 160px;
  margin: 10px;
}

.typingeffect {
  font-size: var(--typing-font);
  margin-bottom: 30px;
  color: #6c757d;
}

nav ul {
  font-size: var(--fontsize-nav);
  text-align: center;
  list-style-type: none;
  justify-content: flex-end;
  display: flex;
  text-decoration: none;
  color: #969696 !important;
}

nav ul li {
  margin: -7px 35px 0px 0px;
  padding: 5px;
  border-radius: 20px;
  box-shadow: -3px -3px 7px #ffffff73, 3px 3px 5px rgba(18, 18, 19, 0.288);
}

nav ul li:hover {
  box-shadow: 5px 5px 15px #919396, -5px -5px 15px #ffffff;
  font-size: 18.5PX;
}

@media screen and (max-width: 1200px) {

