/*Download Cv*/
.btnCv {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 200px;
  height: 70px;
  background: #625e5f;
  color: #999;
  font-size: 20px;
  font-weight: 700;
  border-radius: 50px;
  cursor: pointer;
  text-decoration: none;
  transition: 0.8s;
  transition-delay: 0.5s;
  overflow: hidden;
}

.btnCv .fa {
  margin-right: 8px;
}

.btnCv .download {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  white-space: nowrap;
  transition: 0.3s;
}

.btnCv.active .download {
  transform: translate(-50%, 60px);
  animation: bottom_top 0.5s linear forwards;
  animation-delay: 12s;
}

.btnCv .completed {
  position: absolute;
  top: 0%;
  left: 50%;
  transform: translate(-50%, -60px);
  white-space: nowrap;
  color: rgb(49, 48, 48);
}

.btnCv.active .completed {
  animation: top_bottom 2.3s linear forwards;
  animation-delay: 6.5s;
}

.btnCv.active {
  width: 300px;
  height: 15px;
  animation: btn_width 4.5s linear forwards;
  animation-delay: 5s;
}

.btnCv.active::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background: #fd4520;
  animation: before_active 10s linear forwards;
  animation-delay: 1.5s;
}

.btnCv.active::after {
  content: '\f00c';
  position: absolute;
  top: 50%;
  left: 50%;
  font-family: "Font Awesome 5 Free";
  color: #625e5f;
  transform: translate(-50%, -50%) scale(0);
  transition: 0.3s;
  animation: check_scale 2s linear forwards;
  animation-delay: 8.5s;
}

@keyframes before_active {
  0% {
    width: 0%;
    height: 100%;
  }

  30%,
  95% {
    width: 100%;
    height: 100%;
  }

  100% {
    width: 100%;
    height: 0%;
  }
}

@keyframes btn_width {
  0% {
    width: 300px;
    height: 15px;
  }

  20%,
  70% {
    width: 200px;
    height: 70px;
  }

  80%,
  90% {
    width: 100px;
    height: 100px;
  }

  100% {
    width: 200px;
    height: 70px;
  }
}

@keyframes top_bottom {
  0% {
    transform: translate(-50%, -60px);
  }

  20%,
  90% {
    top: 50%;
    transform: translate(-50%, -50%);
  }

  100% {
    top: 120%;
    transform: translate(-50%, -50%);
  }
}

@keyframes bottom_top {
  0% {
    transform: translate(-50%, 60px);
  }

  100% {
    transform: translate(-50%, -50%);
  }
}

@keyframes check_scale {
  0% {
    transform: translate(-50%, -50%) scale(0);
  }

  20%,
  90% {
    transform: translate(-50%, -50%) scale(1.3);
  }

  100% {
    transform: translate(-50%, 100px) scale(1.3);
  }
}

